import EventBusService from "./EventBusService";
import {EventEmitter2} from "eventemitter2";
import {EventListenerFn} from "./EventListenerFn";

class EventBus implements EventBusService {
    static eventEmitter: EventEmitter2;

    constructor() {
        EventBus.eventEmitter = new EventEmitter2();
    }

    subscribe(event: string, action: EventListenerFn): void {
        EventBus.eventEmitter.on(event, action,);
    }

    unsubscribe(event: string, action: EventListenerFn): void {
        EventBus.eventEmitter.removeListener(event, action);
    }

    send(event: string, ...values: any[]): void {
        EventBus.eventEmitter.emit(event, values);
    }
}

export default EventBus;